import axios  from 'axios'   // axios
import config from './config.js' //
import qs from 'qs'  //

export default function $axios(options){
     return new Promise((resolve , reject)=>{
          const instance = axios.create({
               baseURL : config.baseURL , 
               headers : {} , 
               transformResponse : [function(data){}]
          }) ; 

          instance.interceptors.request.use(config =>{
               //Tip : 1 
               // vuex  loading 

               /*
               //Tip : 2 
               // token    vuex  localStorage 
               if(store.getters.token){
                    config.headers['X-Token'] = getToken() ; // token 
               }
               else{
                    //
               }*/

               //Top : 3 
               //
               if(config.method.toLocaleLowerCase() === 'post'
                    || config.method.toLocaleLowerCase() === 'put' 
                    || config.method.toLocaleLowerCase() === 'delete'){
                    config.data = qs.stringify(config.data)
               }
               return config ;

          } , error => { 
               //
               //Tip : 4 
               //loading 
               console.log('request:' , error) ; 
               
               //
               if (error.code == 'ECONNABORTED' && error.message.indexOf('timeout') != -1){
                    console.log(' timeout ') ;
                    // return service.request(originalRequest); //
               }

               //
               const errorInfo = error.response ; 
               console.log(errorInfo); 

               if(errorInfo){
                    //error = errorInfo.data // catch  Promise.reject 
                    const errorStatus = errorInfo.status ; //404 500 403 ... 
                    router.push({
                         path : `/error/${errorStatus}`
                    })
               }
               return Promise.reject(error);  //(catch)

          }) ; 

          instance.interceptors.response.use(response =>{
               let data ; 
               //IE9  response.data  undefined  response.request.responseText(Stringify)
               if(response.data == undefined){
                    data = JSON.parse(response.request.responseText) ; 
               }else{
                    data = response.data ; 
               }

               // let result = null ; 
               
               //code()
               switch (data.status) {
                    case 200:
                         return data.data;
                         break; 
                    case 514 : 
                         return Promise.reject();
                         break ; 
                    default: 
                         return data.data ; 
                         break ; 
               }
               //code
               // const err = new Error(data.description);
               // err.data = data ; 
               // err.response = response ;

               // return data ; 
          } , err =>{
               if(err && err.response){
                    switch(err.response.status){
                         case 400 : 
                              err.message = '' ; 
                              break ; 
                         case 401 : 
                              err.message = '' ; 
                              break ; 
                         case 403 : 
                              err.message = '' ; 
                              break ; 
                         case 404  : 
                              err.message = `${err.response.config.url}` ; 
                              break; 
                         case 408 : 
                              err.message = ''  ; 
                              break ; 
                         case 500 : 
                              err.message = '' ; 
                              break ; 
                         case 501 : 
                              err.message = '' ; 
                              break ; 
                         case 502 : 
                              err.message = '' ; 
                              break ; 
                         case 503 : 
                              err.message = '' ; 
                              break;
                         case 504 : 
                              err.message = '' ; 
                              break ; 
                         case 505 : 
                              err.message = 'HTTP' ; 
                              break ; 
                         default : 
                         
                    }
               }
               console.log(err) ; 
               // element UI 
               // Message.error(`Error:${err}`);
               return Promise.reject(err) ; //
          });

          instance(options).then((res)=>{
               resolve(res) ; 
               return false ; 
          }).catch((error)=>{
               reject(error);
          });
     })
}




