import Login from '../views/Login.vue'   //
import NotFound  from '../views/404.vue'  //404
import Main from '../views/main.vue'  //main
import Home from '../views/Home.vue' //

let routes = [
		{
			path : '/login' , 
			component : Login  , 
			name : '' , 
			hidden : true 
		},{
			path : '/main' , 
			component : Main , 
			hidden : true 
		},{
			path : '/' , 
			component : Home , 
			iconCls : 'el-icon-message'  , // 
			name : '' , 
			children : [
				{
					path : 'test1' , 
					component : Main  , 
					name : '1'
				} ,
				{
					path : 'test2' , 
					component : Main  , 
					name : '2'
				},
				{
					path : 'test3' , 
					component : Main  , 
					name : '3'
				}
			]	
		},{
			path : '/404' , 
			component : NotFound , 
			name : '' , 
			hidden : true 
		} , {
			path : '*' , 
			hidden : true , 
			redirect : {path : '/404'}
		} 
	]


export default routes ; 






