import axios from './api'   

/**
 * 
 *  URL 
 */
export const query = params =>{
     return axios({
          url: '/api/lottery/lottery.do',
          method : 'get' , 
          params
     })
}

export const mock = params =>{
     return axios({
          url : '/mock' , 
          method : 'get' , 
          params 
     });
}

export const upload = data =>{
     return axios({
          url: '/lottery/lottery.do',
          method : 'post' , 
          data 
     })
}

export default {
     query , 
     mock , 
     upload 
}


